var React = require('react');
var Link = require('react-router').Link;

var Main = React.createClass({
	render: function(){
		return (
			<div>
				<div className="container jumbotron">
					<h1 className="theTitle"> Bozeman.by.trail </h1>
					
					
					<Link to='/trails'> 
						<button className="btn btn-info btn-lg mainbuttonone">Trails</button>
					</Link>
					<Link to='/profile'>
						<button className="btn btn-info btn-lg mainbuttontwo">Profile</button>
					</Link>
					  {this.props.children}
				</div>	
			</div>
			)
	}
});

module.exports = Main;





